import React, { useState } from "react"

const Gallery = ({ children }) => {
  const [active, setactive] = useState(false);
  let isActive = active ? 'active' : '';
  return (
    <div className="gallery-item">
      <div className="gallery-img">
        <img src={children.img[0]} alt={children.name} />
        {children.img.length > 1 && (
          <div className="see-more" onClick={() => setactive(true)} onKeyDown={() => setactive(true)} role="button" tabIndex={0}>
            <p>CLICK TO SEE MORE</p>
          </div>
        )}
      </div>
      <div className={`gallery-img-all ${isActive}`}>
        <div className="gallery-img-all-scroller" onClick={() => setactive(false)} onKeyDown={() => setactive(false)} role="button" tabIndex={0}>
          {/* <div className="close-btn">CLOSE</div> */}
          {children.img.map((vi, ki) => (
            <div className="gallery-img-all-item" key={ki}>
              <img src={vi} alt={`${children.name}-${ki}`} />
            </div>
          ))}
        </div>
      </div>
      <div className="gallery-detail">
        <div className="gallery-name">
          <p className="label">Project Name: </p>
          <p className="value">{children.name}</p>
        </div>
        <div className="gallery-role">
          <p className="label">Role: </p>
          <p className="value">{children.role}</p>
        </div>
        <div className="gallery-year">
          <p className="label">Year: </p>
          <p className="value">{children.year}</p>
        </div>
        <div className="gallery-desc">
          <p className="label">Description: </p>
          <p className="value">{children.desc}</p>
        </div>
        <div className="gallery-url">
          <p className="label">Url: </p>
          <a className="value" rel="noreferrer" href={children.url || '#'} target="_blank">
            <p>{children.url !== '' ? "See App/Website" : "App/Website is unavailble"}</p>
          </a>
        </div>
      </div>
    </div>
  );
};

const ProjectGallery = ({ project_gallery }) => {
  return project_gallery.map((v, k) => (
    <Gallery key={k}>
      {v}
    </Gallery>
  ));
};

export default ProjectGallery;