import React from "react";
import { Helmet } from "react-helmet";

import ProjectGallery from "../components/ProjectGallery";
import { experience, education, skills, contacts, bio, project_gallery, experience_year } from "../datas/CVDatas";
import "../scss/index.scss";

const Line = (props) => <hr className={["small-line", props.white ?? ""].join(" ")} />;

const PaneTitle = (props) => (
  <div className={`pane-title ${props.white ?? ""} ${props.className && props.className}`}>
    <h3>{props.children}</h3>
    <Line white={props.white ?? ""} />
  </div>
);

const Contact = ({ contacts }) => {
  return contacts.map((v, k) => (
    <div key={k} className="contact-item">
      <a href={v.link}>
        <p>{v.text}</p>
        <img src={v.icon} alt={`contact-${v.text}`} />
      </a>
    </div>
  ));
};

const Bio = ({ bio }) => {
  return bio.map((v, k) => (
    <div key={k} className="bio-item">
      <p>{v.label}</p>
      <p>{v.value}</p>
    </div>
  ));
};

const Experience = ({ experience }) => {
  return experience.map((v, k) => (
    <div key={k} className="work-placement-item-cont">
      <div className="work-placement-item-bullet">
        <div className="dot"></div>
      </div>
      <div className="work-placement-item">
        <div className="work-placement-title">
          <p className="job-title">{v.title}</p>
          <p className="year-range">{v.year}</p>
        </div>
        <p className="company-name">{v.company_name}</p>
        <div className="description">
          <p>{v.desc}</p>
        </div>
      </div>
    </div>
  ));
};

const Education = ({ education }) => {
  return education.map((v, k) => (
    <div key={k} className="work-placement-item-cont">
      <div className="work-placement-item-bullet">
        <div className="dot"></div>
      </div>
      <div className="work-placement-item">
        <div className="work-placement-title">
          <p className="job-title">{v.name}</p>
          <p className="year-range">{v.year}</p>
        </div>
        <div className="description">
          <p>{v.major}</p>
        </div>
      </div>
    </div>
  ));
};

// markup
const IndexPage = () => {
  return (
    <main>
      <Helmet>
        <title>Riko Logwirno</title>
        <link
          rel="icon"
          class="js-site-favicon"
          type="image/svg+xml"
          href="https://github.githubassets.com/favicons/favicon.svg"
        />
        <meta name="description" content="Welcome to Riko Logwirno personal CV" />
        <meta property="og:title" content="Riko Logwirno CV and Portofolio" />
        <meta property="og:type" content="website" />
        <meta property="og:url" content="https://rikologwirno.github.io/" />
        <meta name="google-adsense-account" content="ca-pub-1582446010448953" />
        <meta property="og:image" content={require("../images/project-gallery/sensor-helper/main menu 2.jpg").default} />
      </Helmet>
      <div id="top-title">
        <div className="left">
          <h1>RIKO LOGWIRNO</h1>
          <h2>WEB AND MOBILE DEVELOPER</h2>
          <Line white="white" />
        </div>
        <div className="right contact-list">
          <Contact contacts={contacts} />
        </div>
      </div>
      <div id="desc-pic">
        <div id="desc">
          <div>
            <PaneTitle>PROFILE</PaneTitle>
            <div id="profile" className="panel-content">
              <p>
                A reliable Web and Mobile Developer, with a various projects that has been done. With a lot of essential skill I
                have for Web and Mobile Development I am confident that I can provide solution to start and finish your projects.
              </p>
              {experience_year.map((v, k) => (
                <p key={k}>
                  {new Date().getFullYear() - v.year} {v.desc}
                </p>
              ))}
            </div>
          </div>
        </div>
        <div id="pic">
          <img src={require("../images/pp.jpg").default} alt="my-profile-graph" />
        </div>
      </div>
      <div id="experience">
        <div id="work-placement">
          <div className="exp">
            <PaneTitle>WORKING EXPERIENCE</PaneTitle>
            <div className="panel-content">
              <Experience experience={experience} />
            </div>
          </div>
          <div className="exp">
            <PaneTitle>EDUCATION</PaneTitle>
            <div className="panel-content">
              <Education education={education} />
            </div>
          </div>
        </div>
        <div id="bio">
          <PaneTitle>BIO</PaneTitle>
          <div className="panel-content right">
            <Bio bio={bio} />
          </div>
        </div>
      </div>
      <div id="skills">
        <PaneTitle white={"white"}>SKILLS AND EXPERIENCE WITH</PaneTitle>
        <div className="skill-list">
          {skills.map((v, k) => (
            <div key={k} style={{ fontSize: 10 * (5 * (v.rating / 100)) }}>
              {v.text}
            </div>
          ))}
        </div>
      </div>
      <div id="project-gallery">
        <PaneTitle>PROJECT GALLERY</PaneTitle>
        <div className="panel-content">
          <ProjectGallery project_gallery={project_gallery} />
        </div>
      </div>
      <div id="main-footer">
        <div className="left-panel">Build with Gatsby and ReactJS</div>
        <div className="right-panel">&copy; RIKO LOGWIRNO</div>
      </div>
    </main>
  );
};

export default IndexPage;
